<template>
  <div class="container">

    <div v-if="!submitted">
       
      <div class="row">
        <div class="col-12">
          <div v-if="message" class="alert alert-danger" role="alert">
            {{ message }}
          </div>
        </div>
        <div class="col-md-6">
          <h3>Create New Article</h3>
          <div class="form-group">
            <label for="title">Title</label>
            <input
              type="text"
              class="form-control"
              id="title"
              required
              v-model="article.title"
              name="title"
            />
          </div>

          <div class="form-group">
            <label for="description">Description</label>
             <editor
              api-key="kmdqjt7nkqjx0jkoi9qps4wztj5n8ufjzavtlmaxp4xpo5eu"
              class="form-control"
              id="description"
              required
              v-model="article.description"
              
              />
          </div>
        </div>
        <div class="col-md-6 publication-detail">
          <h3>Publication Detail</h3>
          <div class="form-group">
            <label for="short_description">Short Description</label>
            <textarea
              class="form-control"
              id="short_description"
              v-model="article.short_description"
              name="short_description"
            ></textarea>
          </div>
          <div class="form-group">
            <label for="image">Thumbnail</label>
            <input type="file" ref="file"
              class="form-control"
              @change="uploadImage" 
              id="image"
              name="image"
            />
          </div>
          <div class="form-group">
            <label for="category" class="control-label">Category</label>
            <select v-model="article.category_id"  name="category_id" id="category_id" class="form-control" required >
                    <option v-for="(category, index) in articelCategory" :key="index" :value="category.id">{{ category.title }}</option>
            </select>
          </div>
          <div class="form-group">
            <label for="is_visible">Published </label>
            <div class="float-right">
            <Toggle v-model="article.is_visible"   />
            </div>
          </div>

          <button class="btn btn-danger btn-custom" @click="saveArticle">Publish</button>
        </div>
      </div> 
    </div>

    <div v-else>
      <h4>You submitted successfully!</h4>
      <button class="btn btn-success" @click="newArticle">Add</button>
    </div>
  </div>
</template>

<script>
import ArticleDataService from "../services/ArticleDataService";
import ArticleCatDataService from "../services/ArticleCatDataService";
import Toggle from '@vueform/toggle';
import Editor from '@tinymce/tinymce-vue'

export default {
  name: "add-article",
  components: { 
    Toggle, 
    'editor': Editor
  },
  data() {
    return {
      articelCategory:[],
      selectedCategory: '',
      message:null,
      file: '',
      article: {
        id: null,
        title: "",
        description: "",
        short_description: "",
        image:"",
        category_id:"",
        is_visible: true
      },
      submitted: false
    };
  },
  methods: {
    saveArticle() {
      var data = {
        title: this.article.title,
        description: this.article.description,
        short_description: this.article.short_description,
        image:this.file,
        category_id: this.article.category_id
      };
      
      ArticleDataService.create(data)
        .then(response => {
          this.article.id = response.data.id;
          this.submitted = true;
          this.showMessage('');
        })
        .catch(error => {
            let msg =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
            console.log(error);
            this.showMessage(msg)
        });
    },
    uploadImage(){
      this.file = this.$refs.file.files[0];
    },
    newArticle() {
      this.submitted = false;
      this.article = {};
    },
    getCategory(){
      ArticleCatDataService.getAll()
        .then(response => {
          this.articelCategory = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },
    showMessage(msg){
        this.message = msg;  
        setTimeout(() => {
          this.message = "";
        }, 20);
    }
  },
  created(){
    if (!this.currentUser) {
      this.$router.push('/home');

    }
  },
  computed: {
    currentUser() {
      this.getCategory();
      return this.$store.state.auth.user;
    }
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push('/home');
    }
    

  }
};
</script>


<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
@media screen and (max-width: 767px) {
  .publication-detail{
    margin-top:30px;
  }
}
</style>
<style src="@vueform/toggle/themes/default.css"></style>