import http from "../http-common";

class ArticleCatDataService {
  getAll() {
    return http.get("/article-category");
  }

  get(id) {
    return http.get(`/article-category/${id}`);
  }

  create(data) {
    return http.post("/article-category", data);
  }

  update(id, data) {
    return http.put(`/article-category/${id}`, data);
  }

  delete(id) {
    return http.delete(`/article-category/${id}`);
  }

  deleteAll() {
    return http.delete(`/article-category`);
  }

  findByTitle(title) {
    return http.get(`/article-category?title=${title}`);
  }
}

export default new ArticleCatDataService();
